<template>
  <div class="user">
    <div id="user-header">
      <div id="user-header-content">
        <div class="container">
          <div class="row">
            <div class="col-md-3 my-3 avatar-area">
              <img :src="profil.avatar_url" class="img-circle">
            </div>
            <div class="col-md-9 my-3">
              <h1><span class="text-gradient">{{profil.name}}</span></h1>
              <p><a :href="'https://github.com/'+username" target="_blank">https://github.com/{{username}}</a></p>
              <p>Member since {{profil.created_at}}</p>
            </div>
          </div>
        </div>
      </div>
      <div id="user-header-bg">
        <div
          v-for="(b) in bg"
          class="bg-comp"
          :class="'anim-'+b"
          :style="{ backgroundImage: 'url('+require('../assets/img/bg/'+b+'.svg')+')' }">
        </div>
      </div>
    </div>
    <div id="user-res">
      <div class="container">
        <h3 class="text-center my-5" style="line-height: 1.5em"><b class="text-gradient">Capaian Resolusi {{profil.name}} Sampai Saat Ini</b></h3>
        <div v-if="!isLoad" class="row justify-content-center">
          <div class="col-md-3 text-center my-3">
            <CircleProgress :percent="progress.repo" :identifier="1" :key="progress.repo"/>
            <h4>Repository</h4>
            <p class="font-courier my-0" @click="showModalEdit('Repository','repo')"><span class="res-progress" :style="prgColorRepo" title="Edit">{{stat.repo+'/'+resolution.repo}}</span></p>
          </div>
          <div class="col-md-3 text-center my-3">
            <CircleProgress :percent="progress.commit" :identifier="2" :key="progress.commit"/>
            <h4>Commit</h4>
            <p class="font-courier my-0" @click="showModalEdit('Commit','commit')"><span class="res-progress" :style="prgColorCommit" title="Edit">{{stat.commit+'/'+resolution.commit}}</span></p>
          </div>
          <div class="col-md-3 text-center my-3">
            <CircleProgress :percent="progress.star" :identifier="3" :key="progress.star"/>
            <h4>Star</h4>
            <p class="font-courier my-0" @click="showModalEdit('Star','star')"><span class="res-progress" :style="prgColorStar" title="Edit">{{stat.star+'/'+resolution.star}}</span></p>
          </div>
          <div class="col-md-3 text-center my-3">
            <CircleProgress :percent="progress.fork" :identifier="4" :key="progress.fork"/>
            <h4>Fork</h4>
            <p class="font-courier my-0" @click="showModalEdit('Fork','fork')"><span class="res-progress" :style="prgColorFork" title="Edit">{{stat.fork+'/'+resolution.fork}}</span></p>
          </div>
          <div class="col-md-3 text-center my-3">
            <CircleProgress :percent="progress.follower" :identifier="5" :key="progress.follower"/>
            <h4>Followers</h4>
            <p class="font-courier my-0" @click="showModalEdit('Follower','follower')"><span class="res-progress" :style="prgColorFollower" title="Edit">{{stat.follower+'/'+resolution.follower}}</span></p>
          </div>
          <div class="col-md-3 text-center my-3">
            <CircleProgress :percent="progress.pull_req" :identifier="6" :key="progress.pull_req"/>
            <h4>Pull Request</h4>
            <p class="font-courier my-0" @click="showModalEdit('Pull Request','pull_req')"><span class="res-progress" :style="prgColorPullReq" title="Edit">{{stat.pull_req+'/'+resolution.pull_req}}</span></p>
          </div>
        </div>
        <div v-else class="text-center">
          <p><i class="fa fa-spin fa-spinner"></i><br />{{progressLoad}}</p>
        </div>
      </div>
    </div>
    <p class="text-center my-5">
      Copyright (c) 2021 <a href="/">My Github Progress</a>. Created by <a href="https://jhonarendra.github.io">Putu Jhonarendra</a><br /><span class="logout" @click="logout">Logout</span>
    </p>
    <b-modal id="modalEdit" :title="modal_edit.title" hide-footer content-class="bg-black">
      <form v-on:submit.prevent="setResolution">
        <p>Jumlah {{modal_edit.title}} yang ingin dicapai tahun 2021</p>
        <input v-if="modal_edit.key == 'repo'" type="number" class="form-control" v-model="resolution.repo">
        <input v-if="modal_edit.key == 'commit'" type="number" class="form-control" v-model="resolution.commit">
        <input v-if="modal_edit.key == 'star'" type="number" class="form-control" v-model="resolution.star">
        <input v-if="modal_edit.key == 'fork'" type="number" class="form-control" v-model="resolution.fork">
        <input v-if="modal_edit.key == 'follower'" type="number" class="form-control" v-model="resolution.follower">
        <input v-if="modal_edit.key == 'pull_req'" type="number" class="form-control" v-model="resolution.pull_req">
        <p class="text-center mt-3 mb-0">
          <button type="sumbit" class="btn btn-sm btn-primary">OK</button>
        </p>
      </form>
    </b-modal>
  </div>
</template>

<script>
import axios from 'axios'
import firebase from './../services/firebase'
import CircleProgress from './../components/CircleProgress'
import './../assets/css/user.css'
export default {
  data () {
    return {
      username: '',
      fbid: '', // firestore collection id
      profil: {},
      stat: {
        repo: '',
        commit: '',
        star: '',
        fork: '',
        follower: '',
        pull_req: ''
      },
      resolution: {
        repo: 0,
        commit: 0,
        star: 0,
        fork: 0,
        follower: 0,
        pull_req: 0
      },
      progress: {
        repo: 0,
        commit: 0,
        star: 0,
        fork: 0,
        follower: 0,
        pull_req: 0
      },
      modal_edit: {
        title: '',
        key: ''
      },
      bg: [16, 15, 5, 1, 2, 21, 11, 10, 7, 22, 4, 1, 13, 21, 10, 7, 14, 16, 15, 7, 4, 22, 22, 2, 12, 20, 8, 9, 22, 10, 11, 22, 11, 2, 6, 22, 21, 14, 2, 22, 15, 11, 22, 8, 9, 2, 3, 22, 22, 13, 17, 3, 5, 7, 22, 18, 11, 1, 22, 1, 10, 12, 14, 21, 3, 1, 14, 13, 15, 20, 6, 7, 22, 11, 16, 15, 5, 1, 2, 21, 11, 10, 7, 22, 4, 1, 13, 21, 10, 7, 14, 16, 15, 7, 4, 22, 22, 2, 12, 20, 8, 9, 22, 10, 11, 22, 11, 2, 6, 22, 21, 14, 2, 22, 15, 11, 22, 8, 9, 2, 3, 22, 22, 13, 17, 3, 5, 7, 22, 18, 11, 1, 22, 1, 10, 12, 14, 21, 3, 1, 14, 13, 15, 20, 6, 7, 22, 11, 16, 15, 5, 1, 2, 21, 11, 10, 7, 22, 4, 1, 13, 21, 10, 7, 14, 16, 15, 7, 4, 22, 22, 2, 12, 20, 8, 9, 22, 10, 11, 22, 11, 2, 6, 22, 21, 14, 2, 22, 15, 11, 22, 8, 9, 2, 3, 22, 22, 13, 17, 3, 5, 7, 22, 18, 11, 1, 22, 1, 10, 12, 14, 21, 3, 1, 14, 13, 15, 20, 6, 7, 22, 11, 16, 15, 5, 1, 2, 21, 11, 10, 7],
      isLoad: true,
      progressLoad: 'Mengambil data profil'
    }
  },
  components: {
    CircleProgress
  },
  methods: {
    setResolution () {
      if (navigator.onLine) {
        const db = firebase.firestore()
        db.collection('resolution')
          .doc(this.fbid)
          .update({
            resolution: this.resolution
          })
          .then(() => {
            this.$bvModal.hide('modalEdit')
            this.refreshProgress()
            // console.log("Document successfully updated!")
          })
          .catch((error) => {
            console.error('Error updating document: ', error)
          })
      }
    },
    loadGithubProfile () {
      if (navigator.onLine) {
        axios
          .get('https://api.github.com/users/' + this.username)
          .then(res => {
            this.profil = JSON.parse(JSON.stringify(res.data))
            this.profil.created_at = this.profil.created_at.substr(0, 4)
            this.stat.follower = this.profil.followers
          })
      } else {
        // dummy
        this.profil = {
          avatar_url: require('./../assets/img/avatar-1.png'),
          name: 'Jhonarendra',
          created_at: '2015'
        }
        this.username = 'jhonarendra'
        this.stat.follower = 20
      }
    },
    loadGithubRepos () {
      if (navigator.onLine) {
        axios
          .get('https://api.github.com/users/' + this.username + '/repos?per_page=1000')
          .then(res => {
            // this.post = JSON.parse(JSON.stringify(res.data))
            let repos = JSON.parse(JSON.stringify(res.data))
            this.stat.repo = repos.length
            // counting stars
            let stars = 0
            repos.forEach((e) => {
              stars += e.stargazers_count
            })
            this.stat.star = stars
            // counting forks
            let forks = 0
            repos.forEach((e) => {
              forks += e.forks_count
            })
            this.stat.fork = forks
          })
      } else {
        // dummy
        this.stat.repo = 20
        this.stat.star = 20
        this.stat.fork = 20
      }
    },
    loadGithubEvents () {
      if (navigator.onLine) {
        axios
          .get('https://api.github.com/users/' + this.username + '/events?per_page=1000')
          .then(res => {
            let events = JSON.parse(JSON.stringify(res.data))
            let commits = 0
            let pullReqs = 0
            events.forEach((e) => {
              if (e.type === 'PushEvent' && e.created_at.substr(0, 4) === '2021') {
                commits += 1
              }
              if (e.type === 'PullRequestEvent' && e.created_at.substr(0, 4) === '2021') {
                pullReqs += 1
              }
            })
            this.stat.commit = commits
            this.stat.pull_req = pullReqs
          })
      } else {
        // dummy
        this.stat.commit = 20
        this.stat.pull_req = 4
      }
    },
    loadResolution () {
      if (navigator.onLine) {
        const db = firebase.firestore()
        db.collection('resolution')
          .get()
          .then((q) => {
            q.forEach((doc) => {
              if (doc.data().username === this.username) {
                this.resolution = doc.data().resolution
                this.fbid = doc.id
              }
            })
          })
          .catch((error) => {
            console.error('Error getting documents: ', error)
          })
      } else {
        // dummy
        this.resolution = {
          repo: 30,
          commit: 3000,
          star: 30,
          fork: 50,
          follower: 40,
          pull_req: 20
        }

        this.progress = {
          repo: parseInt(this.stat.repo / this.resolution.repo * 100),
          commit: parseInt(this.stat.commit / this.resolution.commit * 100),
          star: parseInt(this.stat.star / this.resolution.star * 100),
          fork: parseInt(this.stat.fork / this.resolution.fork * 100),
          follower: parseInt(this.stat.follower / this.resolution.follower * 100),
          pull_req: parseInt(this.stat.pull_req / this.resolution.pull_req * 100)
        }
        console.log(this.progress)
      }
    },
    refreshProgress () {
      this.progress = {
        repo: parseInt(this.stat.repo / this.resolution.repo * 100),
        commit: parseInt(this.stat.commit / this.resolution.commit * 100),
        star: parseInt(this.stat.star / this.resolution.star * 100),
        fork: parseInt(this.stat.fork / this.resolution.fork * 100),
        follower: parseInt(this.stat.follower / this.resolution.follower * 100),
        pull_req: parseInt(this.stat.pull_req / this.resolution.pull_req * 100)
      }
    },
    showModalEdit (title, key) {
      this.modal_edit.title = title
      this.modal_edit.key = key
      this.$bvModal.show('modalEdit')
    },
    logout () {
      localStorage.removeItem('gr_username')
      this.$router.push({ path: '/' })
    }
  },
  mounted () {

  },
  created () {
    this.username = localStorage.getItem('gr_username')
    this.loadGithubProfile()
    this.loadGithubRepos()
    this.loadGithubEvents()
    this.loadResolution()

    setTimeout(() => {
      this.progressLoad = 'Mengambil data repository'
    }, 3000)
    setTimeout(() => {
      this.progressLoad = 'Mengambil data commit'
    }, 3500)
    setTimeout(() => {
      this.progressLoad = 'Mengambil data star'
    }, 5000)
    setTimeout(() => {
      this.progressLoad = 'Mengambil data fork'
    }, 5900)
    setTimeout(() => {
      this.progressLoad = 'Mengambil data follower'
    }, 6200)
    setTimeout(() => {
      this.progressLoad = 'Mengambil data pull request'
    }, 7000)
    setTimeout(() => {
      this.progressLoad = 'Selesai'
    }, 7100)
    setTimeout(() => {
      this.isLoad = false
      this.refreshProgress()
    }, 7500)
  },
  computed: {
    prgColorRepo () {
      if (this.progress.repo === 0) {
        return 'color: #9E9E9E'
      } else if (this.progress.repo <= 40) {
        return 'color: #FF6969'
      } else if (this.progress.repo <= 80) {
        return 'color: #FFA400'
      } else if (this.progress.repo < 100) {
        return 'color: #5374FA'
      } else if (this.progress.repo === 100) {
        return 'color: #0CCE6B'
      }
    },
    prgColorCommit () {
      if (this.progress.commit === 0) {
        return 'color: #9E9E9E'
      } else if (this.progress.commit <= 40) {
        return 'color: #FF6969'
      } else if (this.progress.commit <= 80) {
        return 'color: #FFA400'
      } else if (this.progress.commit < 100) {
        return 'color: #5374FA'
      } else if (this.progress.commit === 100) {
        return 'color: #0CCE6B'
      }
    },
    prgColorStar () {
      if (this.progress.star === 0) {
        return 'color: #9E9E9E'
      } else if (this.progress.star <= 40) {
        return 'color: #FF6969'
      } else if (this.progress.star <= 80) {
        return 'color: #FFA400'
      } else if (this.progress.star < 100) {
        return 'color: #5374FA'
      } else if (this.progress.star === 100) {
        return 'color: #0CCE6B'
      }
    },
    prgColorFork () {
      if (this.progress.fork === 0) {
        return 'color: #9E9E9E'
      } else if (this.progress.fork <= 40) {
        return 'color: #FF6969'
      } else if (this.progress.fork <= 80) {
        return 'color: #FFA400'
      } else if (this.progress.fork < 100) {
        return 'color: #5374FA'
      } else if (this.progress.fork === 100) {
        return 'color: #0CCE6B'
      }
    },
    prgColorFollower () {
      if (this.progress.follower === 0) {
        return 'color: #9E9E9E'
      } else if (this.progress.follower <= 40) {
        return 'color: #FF6969'
      } else if (this.progress.follower <= 80) {
        return 'color: #FFA400'
      } else if (this.progress.follower < 100) {
        return 'color: #5374FA'
      } else if (this.progress.follower === 100) {
        return 'color: #0CCE6B'
      }
    },
    prgColorPullReq () {
      if (this.progress.pull_req === 0) {
        return 'color: #9E9E9E'
      } else if (this.progress.pull_req <= 40) {
        return 'color: #FF6969'
      } else if (this.progress.pull_req <= 80) {
        return 'color: #FFA400'
      } else if (this.progress.pull_req < 100) {
        return 'color: #5374FA'
      } else if (this.progress.pull_req === 100) {
        return 'color: #0CCE6B'
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="">

</style>



